import React from "react";
import { Paper, Divider } from "@material-ui/core";
import Grid from "@material-ui/core/Grid";
import { makeStyles, MuiThemeProvider } from "@material-ui/core/styles";
import Typography from "@material-ui/core/Typography";
import { storiesOf } from "@storybook/react";
import themes from "../../Theme/themes";
import { BrandType } from "../../types/brand";
import { ProductType } from "../../types/product";
import CardHighlight from "../../CardHighlight";
import { Disney } from "@cvccorp-components/icons";

const useStyles = makeStyles(theme => ({
    root: {
        background: theme.palette.background.default,
        boxShadow: theme.shadows[8],
        padding: "30px"
    },
    darkBg: {
        background: "rgba(0, 0, 0, 0.6)",
        boxShadow: theme.shadows[8],
        padding: "30px"
    }
}));

type CardStoryProps = {
    brand: BrandType;
    product: ProductType;
    title: string;
    subtitle: string;
    img: string;
    icon: boolean;
    mode: string;
};

const CardStory = ({ brand, title, subtitle, img, icon, mode }: CardStoryProps) => {
    const classes = useStyles(themes[brand]);

    return (
        <MuiThemeProvider theme={themes[brand]}>
            <Paper className={classes[mode]} id="checkbox">
                <Typography variant="h3">Controles de seleção</Typography>
                <br />
                <Divider />
                <br />
                <Grid container direction="column" justify="center" alignItems="flex-start" spacing={2}>
                    <Grid item xs={2}>
                        <CardHighlight icon={icon && <Disney size="medium" />} title={title} subtitle={subtitle} img={img} />
                    </Grid>
                </Grid>
            </Paper>
        </MuiThemeProvider>
    );
};

storiesOf("Components/Cards/CardHighlight", module).add("Controls", args => <CardStory {...args} />, {
    args: {
        brand: "CVC",
        title: "Item 2",
        subtitle: "Subtitle 2",
        img: "http://lorempixel.com/output/city-q-c-298-285-3.jpg",
        icon: false,
        mode: "root"
    },
    argTypes: {
        brand: {
            type: "select",
            options: ["BlackAndWhite", "BlackFriday", "CVC", "CVCCorp", "CVCYellow", "Sub"]
        },
        title: { type: "string" },
        subtitle: { type: "string" },
        img: { type: "array" },
        icon: { type: "boolean" },
        mode: {
            type: "select",
            options: ["root", "darkBg"]
        }
    }
});
